@import './util';



.content{
    width: 100%;
    min-height: 100vh;
    background-color: #ffffff;
    // padding-top: 10vmin;
    position: relative;
    padding:5vmin 10vmin 20vmin 10vmin;
    @include maxMobile(600px){
      padding: 5vmin;
    }


    @include flex(space-between,flex-start)
}

.main-font{
    font-family: $logo;
}
.section-partition{
    position: relative;
    z-index: 99;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.144), rgba(0, 0, 0, 0));
}

.fade-in {
    opacity: 0;
    transition: opacity 1s ease-in-out;
    opacity: 1;
  }


.img-placeholder{
    width: 100%;
    height: 100%;
}


.smooth-image {
    transition: opacity 3s;
  }
  .image-visible {
    opacity: 1;
  }
  .image-hidden {
    opacity: 0;
  }

  .srchBox{
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    width:60%;
    margin:0 auto;
    @include maxMobile(900px){
      width: 80%;
    }
    @include maxMobile(636px){
      width: 85%;
    }
  }