@import '../util';

nav{
    width:100%;
    height:15vh;
    @include flex;
    padding: 0 10vmin;
    position: sticky;
    top:0;
    left:0;
    // background-color: var(--bg);
    z-index: 9999;
  @include maxMobile(1100px){
    // background-color: red;
    font-size: 13px;

  }
  @include maxMobile(932px){
    padding: 0 5vmin;
  }
@include maxMobile(767px){
font-size: 11px;
}
@include maxMobile(600px){
    padding: 0 10vmin;
    height: 10vh;
}

    
    #nlft{
        width: 60%;
        height: initial;
    @include flex;
    @include maxMobile(932px){
        // background-color: red;
        width: 70%;
    
      }
      @include maxMobile(636px){
        width: 75%;
      }
   

    }
    #nrt{
        width: 40%;
        height: inherit;
        @include flex;
        padding-left: 5vmax;
        @include maxMobile(932px){
            // background-color: red;
            width: 20%;
        
          }
          @include maxMobile(767px){
            width: 25%;
          }
          @include maxMobile(600px){
            width: fit-content
        }

        #nSrch{
            @include flex;
            width: 70%;
            border-radius: 2em;
            background-color: #ffffff1a;
            padding: .5em 1em;
            input{
                background-color: transparent;
                border:none;
                width: 90%;
                outline: none;
                color:#fff;
                &::placeholder{
                    color:var(--action2);
                }
            }
            @include maxMobile(932px){
                display: none;
            
              }
        }
        >.icons-class{
            display: none;
          
        }
        #n-srch{
            @include maxMobile(932px){
                display: initial;
            
              }
            @include maxMobile(600px){
                display: none;
            }
         
        }
        #n-menu{
            @include maxMobile(600px){
                display: initial;
            }
        }
    }
    #nLinks{
        @include maxMobile(600px){
            display: none;
        }
    }
    .n-joinBtn{
        @include maxMobile(600px){
            display: none;
        }
    }
    
    

}
.logo{
    h3{
        font-family: $logo;
        color:var(--primTxt);
        font-weight: 200;
        font-size: 2em;
        letter-spacing: .1em;

    }
    h5{
        font-family: $cursive;
        color:var(--action2);
        font-size: 3em;
        font-weight: 100;
        margin-top: -.7em;
    }

}
.onscroll-class{
    // background-color: var(--card);
    // box-shadow: 0 0 30px #0000003b;
    backdrop-filter: blur(30px);
    transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}
.other-scroll-class{
    // background-color: #ffffff;
    backdrop-filter: blur(30px);
    box-shadow: 0 0 30px #0000003b;
    .logo{
        h3{
            color:var(--darkLogo);
        }
    }
    .n-link{
        color:var(--darkLogo)
    }
    .nav-activeLink{
        color:#D9AC72;
        
    }
    #nrt{
       #nSrch{
        background-color: #00000011;
    }  
    }
   


}

.n-link{
    text-decoration:none;
    color:#fff;
    text-transform:uppercase;
    font-weight:600;
    font-size:.8em;
    margin-right:1em;
    letter-spacing:.1em;
  
}

.nav-activeLink{
    color:#D9AC72;
    
}
