@import '../../../_util.scss';

#a-section-lft{
    // background-color: rgb(39, 196, 52);
    width: 70%;
    min-height: inherit;
    @include maxMobile(1048px){
        width: 100%;
    }
}
#a-section-rt{
    // background-color: yellow;
    width: 30%;
    height: 70vh;
    position: sticky;
    top:20vmin;
    border-left: 1px solid #00000017;
    padding: 1em 2em;
    @include maxMobile(1048px){
        display: none;
    }
}
.top-org-card{
    padding: .5em;
    @include flex(space-between,center);
    // background-color: red;
    margin-top: 1em;
    // img{
    //     width: 50%;
    // }
}
.articleContainer{
    width:100%;
    padding-right:5vmin;
    margin-top:5vmin;
    display: flex;
    align-items: center;
}

.writeNowBtn{
    display: none !important;
    position: fixed !important;
        bottom:10vmin !important;
        right:10vmin !important;

    @include maxMobile(1048px){
        display:flex !important;
        align-items: center !important;
        
    }

}