@import '../util';

.auth{
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 3;
    @include flex(flex-end,flex-end);
    .aRt{
        width: 60%;
        height:100%;
        // backdrop-filter: blur(10px);
        background-color: #fff;
        overflow-y: auto;
        // padding-bottom: 2em;
        @include maxMobile(690px){
            width: 80%;
        }
        .sideButtons{
            display: none;
            // background-color: red;
            padding: 0;
            width: 100%;
            @include maxMobile(690px){
                display:flex;
            }

        }
    }
   
}
.uForm{
    // background-color: red;
    width: 60%;
    @include maxMobile(1000px){
        width: 80%;
    }
    @include maxMobile(740px){
        width: 90%;
    }
}

.sideButtons{
    display:flex;
    flex-direction:column;
    width:50%;
    padding: 10vmin;
    @include maxMobile(690px){
        display: none;
    }
}

.orgFormBox{
    width: 100%;
    min-height:50vh;
    // background-color: red;
    // overflow-y: auto;
    padding:2em 0;
    form{
        width:80%;
        @include maxMobile(740px){
            width: 100%;
        }
    }
}

.orgFields{
    margin-bottom: 1em;
}