@import "../../../util";

.articleCard{
    // background-color: red;
    min-height: 50vh;
    border-radius: 1em;
    @include flex(flex-start,center);
    padding: 0 1em;
    position:relative;
    @include maxMobile(1048px){
        width: 700px;
    }
    @include maxMobile(800px){
        width: 600px;
        min-height: 40vh;
    }
    @include maxMobile(700px){
        width: 500px;
        min-height: 40vh;
    }
    @include maxMobile(600px){
        @include flex(center,flex-end,column);
        padding:5vmin;
        width: 330px;
    }

    #leftQuoteA{
        font-size: 5vmin;
        background-color:var(--action1);
        height:inherit;
        color:#fff;
        box-shadow: 0 5px 5px #00000021;
        @include maxMobile(600px){
            margin-bottom: 5vmin;
        }
    }
    .aImg{
        width:50%;
        @include maxMobile(600px){
            width: 100%;
        }
    }
    img{
        width: 100%;
        // height: 20vh;

        // color:#5e5c5c;

    }
    .articleCardContent{
        width: 60%;
        height: inherit;
        @include flex(space-between,flex,column);
        padding: 0 1em 1em 1em;
        position: relative;
        z-index: 2;
        @include maxMobile(600px){
            width: 100%;
        }

    }
    #rightQuoteA{
        position: absolute;
        right:0;
        font-size: 20vmin;
        bottom:0;
        color:#d9ac7252;
        z-index: 1;
    }
    .aIntro{
        display: flex;
        justify-content: space-between;
        @include maxMobile(600px){
            margin-top: 5vmin;
        }
    }
    .aAction{
        // background-color: yellow;
        width: 40%;
        display: flex;
        align-items: center;
    }
}